import React, { Component } from 'react';
import Logo from '../img/logo.png'
import {Row , Container , Col, Navbar , Nav, NavDropdown} from 'react-bootstrap';
class topbar extends Component {
    state = {  }
    render() {
        return (
            <div>
                        <Container fluid >
                            <Row className="topbar">
                                    <Col lg={3} sm={6} className="topbar-social">
                                        <i className='fa fa-facebook icon' ></i>
                                        <i className='fa fa-pinterest-p icon' ></i>
                                        <i className='fa fa-github icon' ></i>
                                    </Col>
                                    <Col lg={9} sm={6} xs={12} className="topbar-contact">
                                         <div ><i className='fa fa-phone ' ></i>969999000</div>
                                        <div ><i className='fa fa-envelope '></i>genwebtechnology@gmail.com</div>  
                                    </Col> 
                            </Row>
                            
                        <Navbar collapseOnSelect expand="lg" bg="transparment" variant="dark">
                                    <Navbar.Brand href="#home">
                                    <img
                                            src={Logo}
                                       
                                            className="d-inline-block align-top"
                                            alt="Genweb Technology" />

                                    </Navbar.Brand>
                                    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                                        <Navbar.Collapse id="responsive-navbar-nav">
                                                <Nav className="ml-auto">
                                                <Nav.Link href="#features" >Home</Nav.Link>
                                                <Nav.Link href="#pricing" >Service</Nav.Link>
                                                <Nav.Link href="#pricing" >About us</Nav.Link>
                                                <Nav.Link href="#pricing" >Contact us</Nav.Link>
                                                </Nav>
                                        </Navbar.Collapse>
                        </Navbar>
                                    
                           
                            </Container>
                            
                    
            </div>
            
        );
    }
}

export default topbar;
