import React, { Component } from 'react';
import Slider from "react-slick";
import {Row , Container , Col} from 'react-bootstrap';
import Logo1 from '../img/logo1.png';
import Logo2q from '../img/logo8.png';
import Logo3q from '../img/logo3q.png';
import Logo4 from '../img/logo4.png';
import Logo5 from '../img/logo5.png';
import Logo6 from '../img/logo6.png';
class logoSlider extends Component {
    state = {  }
    render() {
        const settings = {
            dots: false,
            arrows : false,
            infinite: true,
            speed: 500,
            slidesToShow: 5,
            slidesToScroll: 1,
            autoplay: true,
            speed: 2000,
            autoplaySpeed: 2000,
            responsive: [
                {
                  breakpoint: 1024,
                  settings: {
                    slidesToShow: 3,
                    slidesToScroll: 3,
                    infinite: true,
                    dots: true
                  }
                },
                {
                  breakpoint: 600,
                  settings: {
                    slidesToShow: 2,
                    slidesToScroll: 2,
                    initialSlide: 2
                  }
                },
                {
                  breakpoint: 480,
                  settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1
                  }
                }
              ]
            
          };
        return (
            <div>
                <Container>
                <Slider {...settings}>
                <div className="sliderContent">
                    <div className="LogosliderImg"> <img
                                            src={Logo1}
                                            className="d-inline-block align-top"
                                            alt="Genweb Technology" /></div>
                  
                </div>
                <div  className="sliderContent">
                <div className="LogosliderImg"><img
                                            src={Logo2q}
                                            className="d-inline-block align-top"
                                            alt="Genweb Technology" /></div>
                   
                </div >
                <div className="sliderContent">
                <div className="LogosliderImg"><img
                                            src={Logo3q}
                                            className="d-inline-block align-top"
                                            alt="Genweb Technology" /></div>
                    
                </div>
                <div className="sliderContent">
                <div className="LogosliderImg"><img
                                            src={Logo4}
                                            className="d-inline-block align-top"
                                            alt="Genweb Technology" /></div>

                </div>
                <div className="sliderContent">
                <div className="LogosliderImg"><img
                                            src={Logo5}
                                            className="d-inline-block align-top"
                                            alt="Genweb Technology" /></div>

                </div>
                <div className="sliderContent">
                <div className="LogosliderImg"><img
                                            src={Logo6}
                                            className="d-inline-block align-top"
                                            alt="Genweb Technology" /></div>

                </div>
                </Slider>
                </Container>
                <div className="spaceTestimonial"></div>
                <div className="divider"></div>
                <div className="space"></div>
            </div>
            
        );
    }
}

export default logoSlider;