import React, { Component } from 'react';
import {Row , Container , Col,} from 'react-bootstrap';
import Logo from '../img/logo.png'
class footer extends Component {
    state = {  }
    render() {
        return (
            <div>
                <Container>
                <Row>
                    <Col  xs={12} sm={3} className="fLogo">
                    <img   src={Logo}
                    className="d-inline-block align-top"
                     alt="Genweb Technology" />
                     <p>Lorem ipsum dolor consectetur adipiscing elit, eiusmod tempor ut labore et dolore magna aliqua.</p>
                    </Col>
                    <Col xs={12} sm={3} className="footerTitle">
                    <h3>Quick Links</h3>
                    <ul>
                        <li>About</li>
                        <li>Portfolio</li>
                        <li>Price</li>
                        <li>Home</li>

                    </ul>
                    </Col>
                    <Col xs={12} sm={3} className="footerTitle">
                    <h3>Social Media</h3>
                    <div className="footer-social">
                                        <i className='fa fa-facebook ficon' ></i>
                                        <i className='fa fa-pinterest-p ficon' ></i>
                                        <i className='fa fa-github ficon' ></i>
                                        <i className='fa fa-facebook ficon' ></i>
                                    </div>
                    </Col>
                    <Col xs={12} sm={3} className="footerTitle">
                    <h3>Contact</h3>
                    <ul>
                        <li>Address: Bhubaneswar India</li>
                        <li>Pin: 754001</li>
                        <li>Email: genwebtechnology@gmail.com</li>
                        <li>Phone: 99999999</li>

                    </ul>
                    </Col>
                </Row>
                <div className="footerSpace"></div>
                <div className="goToTop">
                <i className='fa fa-chevron-right' ></i>
                </div>
                <div className="copyRightText">All Rights Reserved, 2019.</div>
                </Container>
            </div>
            
        );
    }
}

export default footer;