import React, { Component } from 'react';
import {Row , Container , Col,} from 'react-bootstrap';
import { Button, FormFeedback, Form, FormGroup, Label, Input } from 'reactstrap'
import Map from '../img/map2.png';
import * as emailjs from 'emailjs-com';
class contactusSection extends Component {
    state = {
        name: '',
        email: '',
        subject: '',
        message: '',
      }
    handleSubmit(e) {
        e.preventDefault()
        const { name, email, subject, message } = this.state
        let templateParams = {
          from_name: email,
          to_name: 'genwebtechnology@gmail.com',
          subject: subject,
          message_html: message,
         }
         emailjs.send(
          'gmail',
          'template_OxfE1Cvq',
           templateParams,
          'user_wBCRGiSbMQGWKdLAA5bG3'
         )
         this.resetForm()
     }
    resetForm() {
        this.setState({
          name: '',
          email: '',
          subject: '',
          message: '',
        })
      }
    handleChange = (param, e) => {
        this.setState({ [param]: e.target.value })
      }
    render() {
        return (
            <div >
                 <Container>
                          <Row className="parentGrid">
                              <Col>
                                   <div className="contactusTitle">About us</div>
                                   <div className="contactusHeader"><h3>Get in Touch</h3></div>
                                   
                              </Col>
                            
                              
                            </Row>
                            <div className="cSpace"></div>

                            <Row className="parentGrid  contactGrid">
                            <Col  xs={12} sm={6}>
                            <div className="contactusImg"></div>
                            <div className="blob"></div>
                            <div className="toolTip">
                                <h5>India</h5>
                                <p>Bhubaneswar</p>
                            </div>
                            <div className="ToolTip-tri"></div>
                                           
                              </Col>
                              <Col  xs={12} sm={6}>
                              <Form onSubmit={this.handleSubmit.bind(this)}>
                                        <FormGroup controlId="formBasicEmail">
                                        
                                        <Input
                                            type="email"
                                            name="email"
                                            value={this.state.email}
                                            className="text-primary"
                                            onChange={this.handleChange.bind(this, 'email')}
                                            placeholder="Enter email"
                                        />
                                        </FormGroup>
                            <FormGroup controlId="formBasicName">
                                      
                                        <Input
                                            type="text"
                                            name="name"
                                            value={this.state.name}
                                            className="text-primary"
                                            onChange={this.handleChange.bind(this, 'name')}
                                            placeholder="Name"
                                        />
                                        </FormGroup>
                            <FormGroup controlId="formBasicSubject">
                                        
                                        <Input
                                            type="text"
                                            name="subject"
                                            className="text-primary"
                                            value={this.state.subject}
                                            onChange={this.handleChange.bind(this, 'subject')}
                                            placeholder="Subject"
                                        />
                                        </FormGroup>
                            <FormGroup controlId="formBasicMessage">
                                       
                                        <Input
                                            type="textarea"
                                            name="message"
                                            className="text-primary"
                                            value={this.state.message}
                                            onChange={this.handleChange.bind(this, 'message')}
                                            placeholder="Type your message here."
                                        />
                                        </FormGroup>
                            <Button variant="primary" type="submit" className="Submit">
                                        Submit
                                        </Button>
                                    </Form>

                              </Col>
                             
                              
                            </Row>
                            </Container>
                            <div className="space"></div>
            </div>
            
        );
    }
}

export default contactusSection;