import React, { Component } from 'react';
import Slider from "react-slick";
import {Row , Container , Col} from 'react-bootstrap';
import Slider1 from '../img/slider1.jpg';
import Slider2 from '../img/slider2.png';

class projectSection extends Component {
    state = {  }
    render() {
        const settings = {
            dots: false,
            arrows : false,
            infinite: true,
            speed: 500,
            slidesToShow: 3,
            slidesToScroll: 1,
            autoplay: true,
            speed: 3000,
            autoplaySpeed: 3000,
            responsive: [
                {
                  breakpoint: 1024,
                  settings: {
                    slidesToShow: 3,
                    slidesToScroll: 3,
                    infinite: true,
                    dots: true
                  }
                },
                {
                  breakpoint: 600,
                  settings: {
                    slidesToShow: 2,
                    slidesToScroll: 2,
                    initialSlide: 2
                  }
                },
                {
                  breakpoint: 480,
                  settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1
                  }
                }
              ]
            
          };
        return (
            <div>
        <Container>
                          <Row className="parentGrid">
                              <Col xs={12}>
                                   <div className="projectTitle">OUR COMPLETED PROJECTS</div>
                                   <div className="projectHeader"><h3>Recent Projects</h3></div>
                                   
                              </Col>
                              <Col>
                                    
                              </Col>
                            </Row>
                            </Container>
                <Container className="sliderParent">
        
                <Slider {...settings}>
                <div className="sliderContent">
                    <div className="sliderImg"> <img
                                            src={Slider1}
                                            className="d-inline-block align-top"
                                            alt="Genweb Technology" /></div>
                    <div className="sliderText">
                    <div className="sliderTitle"><h4>Tour projects</h4></div>
                    <div className="sliderpara"><p>Wordpress Woocommerce</p></div>
                    </div>
                </div>
                <div  className="sliderContent">
                <div className="sliderImg"><img
                                            src={Slider2}
                                            className="d-inline-block align-top"
                                            alt="Genweb Technology" /></div>
                    <div className="sliderText">
                    <div className="sliderTitle"><h4>Tour projects</h4></div>
                    <div className="sliderpara"><p>lorem ipsum dollar sitc</p></div>
                    </div>
                </div >
                <div className="sliderContent">
                <div className="sliderImg"><img
                                            src={Slider1}
                                            className="d-inline-block align-top"
                                            alt="Genweb Technology" /></div>
                    <div className="sliderText">
                    <div className="sliderTitle"><h4>Tour projects</h4></div>
                    <div className="sliderpara"><p>lorem ipsum dollar sitc</p></div>
                    </div>
                </div>
                <div className="sliderContent">
                <div className="sliderImg"><img
                                            src={Slider2}
                                            className="d-inline-block align-top"
                                            alt="Genweb Technology" /></div>
                    <div className="sliderText">
                    <div className="sliderTitle"><h4>Tour projects</h4></div>
                    <div className="sliderpara"><p>lorem ipsum dollar sitc</p></div>
                    </div>
            
                </div>
                </Slider>
        </Container>
        <div className="space"></div>
                </div>
            
        );
    }
}

export default projectSection;